.CommentInput__picture {
    width: 50px;
    height: 50px;
    margin-right: 12px;
    background-color: #e6e6e6;
    border: 1px solid #f3f3f3;
}

.CommentInput textarea {
    border: 2px solid #e5e5e5;
    border-radius: 1px;
    resize: none;
    height: 68px;
    flex: 1;
}



